@import url(https://fonts.googleapis.com/css2?family=Catamaran:wght@400;700&family=Special+Elite&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

* {
  padding: 0;
  box-sizing: border-box;
}

:root {
  /*colors*/
  --main-color: rgb(255, 255, 255);
  --main-color-semi-transparent: rgba(255, 255, 255, 0.5);
  --main-color-semi: rgba(255, 255, 255, 0.7);
  --content-color: rgb(0, 0, 0);
  --content-color-semitransparent: rgba(0, 0, 0, 0.5);
  /*fonts*/
  --font-primary: "Special Elite", cursive;
  --font-secondary: "Catamaran", sans-serif;
  --font-size-normal: 16px;
  --font-size-normal2: 18px;
  --font-size-small: 14px;
  --font-size-medium: 32px;
  --fonr-size-medium2: 28px;
  --font-size-large: 64px;
  /*spacings*/
  --xxs: 12px;
  --xs: 16px;
  --xs2: 24px;
  --s: 32px;
  --m: 48px;
  --l: 64px;
  --xl: 128px;
}

.App {
  text-align: center;
  min-height: 100vh;
  background-image: url(/GrupaIT3/static/media/forest1920.6c6324d8.jpg);
  background-attachment: fixed;
  background-size: cover;
}

.header {
  height: 15vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: var(--content-color-semitransparent);
  box-shadow: 0px 5px 20px 1px white;
}

.header p {
  font-family: "Special Elite", cursive;
  color: white;
  font-size: var(--font-size-medium);
  text-shadow: 5px 5px var(--content-color-semitransparent);
}

@media (min-width: 768px) {
  .header p {
    font-size: var(--font-size-large);
  }
}

.content {
  min-height: 80vh;
  padding: var(--xxs);
}

.container {
  width: 100%;
  max-width: 1400px;
  min-height: 70vh;
  display: flex;
  align-items: flex-start;
  flex-wrap: wrap;
  margin: 0 auto;
  justify-content: center;
  /* padding: 0 200px; */
}

@media (min-width: 768px) {
  .content {
    padding: var(--s);
  }
}
@media (min-width: 991px) {
  .content {
    padding: var(--s);
  }
  .container {
    padding: 0 var(--xl);
  }
}

@media (min-width: 1199px) {
  .content {
    padding: var(--m);
  }
  .container {
    padding: 0 200px;
  }
}

.card {
  width: 100%;
  min-height: 55vh;
  background: var(--main-color-semi);
  border-radius: 12px;
  box-shadow: 0px 0px 15px 3px var(--main-color);
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: var(--s);
  opacity: 0;
  transform: translateY(-200%);
  transition: all 0.5s ease-in-out;
  margin: var(--s) var(--xs);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  box-shadow: 0px 0px 15px 6px var(--content-color-semitransparent);
}

.card div {
  width: 100%;
  margin: var(--xxs) 0;
}

.card div:nth-child(odd) {
  height: 25px;
  background-size: contain;
  background-repeat: no-repeat;
}

.card div:nth-child(1) {
  background-image: url(/GrupaIT3/static/media/quote2.fbb55601.png);
}
.card div:nth-child(3) {
  background-image: url(/GrupaIT3/static/media/quote1.8380480f.png);
  background-position: right;
}
.card div:nth-child(2) {
  font-family: var(--font-secondary);
  font-size: var(--xs2);
}

.card div:nth-child(4) {
  font-family: "Special elite";
  font-size: var(--xs2);
  text-align: right;
  margin-top: var(--s);
}

.card div:nth-child(4)::before {
  content: "--   ";
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@media (min-width: 768px) {
  .card {
    padding: var(--xs2) var(--m);
  }
  .card div {
    margin: var(--xs) 0;
  }
  .card div:nth-child(odd) {
    height: 30px;
  }
  .card div:nth-child(2) {
    font-size: var(--s);
  }
  .card div:nth-child(4) {
    font-size: var(--fonr-size-medium2);
  }
}

@media (min-width: 1199px) {
  .card {
    padding: var(--m) var(--xl);
    background: var(--main-color-semi-transparent);
  }
}

.btn {
  font-size: var(--font-size-normal2);
  padding: var(--xxs) var(--xs2);
  background-color: var(--main-color-semi);
  font-family: var(--font-secondary);
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: 0.2s;
  margin: var(--s) var(--xs);
  width: 100%;
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  box-shadow: 0px 0px 15px 3px var(--content-color-semitransparent);
}
.btn:hover {
  background: white;
  box-shadow: 0px 2px 5px 1px var(--content-color);
}

@media (min-width: 768px) {
  .btn {
    width: 40%;
  }
}

@media (min-width: 1199px) {
  .btn {
    background-color: var(--main-color-semi-transparent);
  }
}

